"use client";

import { useState, Suspense } from "react";
import { useRouter, useSearchParams } from "next/navigation";
import { toast } from "react-hot-toast";
import {api as axios} from "@/lib/axios";
import { 
  ClipboardList, 
  Hash, 
  DollarSign, 
  MessageSquare, 
  ArrowLeft,
  Info
} from "lucide-react";
import Link from "next/link";

// We wrap the content in a separate component to use useSearchParams safely in Next.js 13+
function RFQForm() {
  const router = useRouter();
  const params = useSearchParams();
  const productId = params.get("productId");

  const [quantity, setQuantity] = useState("");
  const [targetPrice, setTargetPrice] = useState("");
  const [message, setMessage] = useState("");
  const [loading, setLoading] = useState(false);

  const submitRFQ = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!productId) {
      toast.error("Product information missing. Please select a product first.");
      return;
    }

    setLoading(true);
    try {
      await axios.post("/rfq", {
        productId,
        quantity: Number(quantity),
        targetUnitPrice: targetPrice ? Number(targetPrice) : undefined,
        message,
      });

      toast.success("RFQ submitted successfully");
      router.push("/buyer/rfq/list");
    } catch (err: any) {
      toast.error(err.response?.data?.message || "Failed to submit RFQ");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="max-w-3xl mx-auto py-10 px-4">
      {/* Breadcrumb / Back Navigation */}
      <Link 
        href="/dashboard" 
        className="flex items-center text-sm text-slate-500 hover:text-blue-600 transition-colors mb-6 group"
      >
        <ArrowLeft className="mr-2 h-4 w-4 group-hover:-translate-x-1 transition-transform" />
        Back to Products
      </Link>

      <div className="bg-white rounded-2xl shadow-sm border border-slate-200 overflow-hidden">
        {/* Header */}
        <div className="bg-slate-900 p-8 text-white">
          <div className="flex items-center gap-3 mb-2">
            <ClipboardList className="text-blue-400" size={28} />
            <h1 className="text-2xl font-bold tracking-tight">Request for Quotation</h1>
          </div>
          <p className="text-slate-400 text-sm">
            Complete the form below to receive pricing and availability from vendors.
          </p>
        </div>

        <form onSubmit={submitRFQ} className="p-8 space-y-8">
          {/* Form Grid */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            
            {/* Quantity Input */}
            <div className="space-y-2">
              <label className="flex items-center gap-2 text-sm font-semibold text-slate-700">
                <Hash size={16} className="text-slate-400" />
                Required Quantity
              </label>
              <input
                type="number"
                required
                min={1}
                placeholder="e.g. 500"
                value={quantity}
                onChange={(e) => setQuantity(e.target.value)}
                className="w-full px-4 py-3 bg-slate-50 border border-slate-200 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all outline-none"
              />
            </div>

            {/* Target Price Input */}
            <div className="space-y-2">
              <label className="flex items-center gap-2 text-sm font-semibold text-slate-700">
                <DollarSign size={16} className="text-slate-400" />
                Target Unit Price <span className="text-slate-400 font-normal font-xs">(Optional)</span>
              </label>
              <input
                type="number"
                placeholder="0.00"
                value={targetPrice}
                onChange={(e) => setTargetPrice(e.target.value)}
                className="w-full px-4 py-3 bg-slate-50 border border-slate-200 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all outline-none"
              />
            </div>
          </div>

          {/* Requirements Textarea */}
          <div className="space-y-2">
            <label className="flex items-center gap-2 text-sm font-semibold text-slate-700">
              <MessageSquare size={16} className="text-slate-400" />
              Detailed Requirements
            </label>
            <textarea
              rows={5}
              placeholder="Describe delivery timelines, customization needs, or specific packaging requirements..."
              value={message}
              onChange={(e) => setMessage(e.target.value)}
              className="w-full px-4 py-3 bg-slate-50 border border-slate-200 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all outline-none resize-none"
            />
          </div>

          {/* Pro-Tip Info Box */}
          <div className="flex gap-3 bg-blue-50 p-4 rounded-xl border border-blue-100">
            <Info className="text-blue-600 shrink-0" size={20} />
            <p className="text-sm text-blue-800">
              <strong>Tip:</strong> Being specific about your delivery date and location helps vendors provide more accurate quotes.
            </p>
          </div>

          {/* Submit Button */}
          <button
            disabled={loading}
            className="w-full py-4 bg-blue-600 hover:bg-blue-700 text-white font-bold rounded-xl shadow-lg shadow-blue-200 transition-all active:scale-[0.99] disabled:opacity-70 disabled:cursor-not-allowed flex justify-center items-center gap-2"
          >
            {loading ? (
              <span className="flex items-center gap-2">
                <div className="h-4 w-4 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                Processing Request...
              </span>
            ) : (
              "Submit Quotation Request"
            )}
          </button>
        </form>
      </div>
    </div>
  );
}

// Main Page Export with Suspense for useSearchParams
export default function CreateRFQPage() {
  return (
    <Suspense fallback={
      <div className="min-h-screen flex items-center justify-center">
        <div className="animate-pulse text-slate-400 font-medium">Loading form...</div>
      </div>
    }>
      <RFQForm />
    </Suspense>
  );
}